*{
    margin: 0;
    padding: 0;
}

.mdiv{
    background:linear-gradient(to bottom,rgb(111, 223, 180),rgb(219, 240, 203));
    height: 60vh;
    margin-top: 10vh;
    margin-left: 27vw;
    margin-right: 27vw;
     border: none;
     border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.mdiv input{
    height: 2rem;
    width: 20vw;
    border-color: white ;
    border-radius:5px;
    margin-bottom: 5px;
    padding-left: 10px;
    font-family:'Times New Roman', Times, serif;
    font-weight: bold;
}

.btn{
    cursor: pointer;
    width: 10vw;
    height: 5vh;
    margin-top: 10px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: bold;
    background:linear-gradient(to right,rgb(202, 197, 197),rgb(137, 133, 133));
    transition: all ease-in-out 0.2s;
    border-radius: 5px;
}

.btn:hover{
    transform: scale(1.04);
    background: linear-gradient(to left,rgb(170, 169, 169),rgb(92, 88, 72));
    color: white;

}

.btn2{
    cursor: pointer;
    width: 10vw;
    height: 5vh;
    margin-top: 10px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: bold;
    background:linear-gradient(to right,rgb(202, 197, 197),rgb(137, 133, 133));
    color: rgb(255, 254, 252);
    border-radius: 5px;
    transition: all ease-in-out 0.2s;

}

.btn2:hover{
    transform: scale(1.04);
    background: linear-gradient(to left,rgb(170, 169, 169),rgb(92, 88, 72));
    color: rgb(38, 36, 36);

}


/* random */
.c1{
    color: wheat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    

}

.hideimg{
    height: 3vh;
    width: 1.5vw;
    
}

.hbtn{
    margin-top: 5px;
    cursor: pointer;
    height: 4vh;
    width: 2vw;
    border-radius: 10px;
}



/* Signup */

#simg{
    position:relative;
    left: 47%;
    margin-top: 5vh;
    border: 5px solid rgb(230, 230, 103);
    border-radius: 50%;
    animation: rotate 4s linear infinite ;
}

@keyframes rotate {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

.sdiv{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to bottom,rgb(72, 191, 185),rgb(102, 176, 233));
    height: 70vh;
    margin-top: 7vh;
    margin-left: 30vw;
    margin-right: 30vw;
     border: none;
     border-radius: 5px;
    
}

.sdiv input{
    height: 2rem;
    width: 20vw;
    border-color: white ;
    border-radius:5px;
    margin-bottom: 5px;
    padding-left: 10px;
    font-family:'Times New Roman', Times, serif;
    font-weight: bold;
}

.content{
    overflow: auto;
    max-height: 100%;
}

/* resposive */

@media (max-width: 767px) {

    .mdiv{
        margin-left: 10vw;
        margin-right: 10vw;
    }

    .mdiv input{
        height: 1.5rem;
        width:50%;
    }
    .hideimg{
        height: 1.5vh;
        width: 2vw;
    }
    .hbtn{
        height: 2.3vh;
        width: 3.5vw;
    }

    
    .btn{
        width:20%;
        height: 1.5rem;
    }
    .btn2{
        width: 35%;
        height: 2rem;
        color: black;
    }
    .l{
        width:60%;
        text-align: center;
    }


    /* signup */
    #simg{
        margin-top: 3vh;
        left: 40%;
        height: 70px;
        width: 70px;
    }

    .sdiv{
        margin-top: 5vh;
        margin-left: 13vw;
        margin-right: 13vw;
    }

    .sdiv input{
        width: 60%;
    }

  }
